.label {
	margin: 7px 0;
	color: #888;
	text-align: left;
}


@keyframes load {
	from {transform: translate(0, 0)}
	to {transform: translate(390px, 0)}
}

@keyframes loadcenter {
	0% {
		width: 0;
	}
	50% {
		width: 50%;
	}
	100% {
		width: 100%;
	}
}

.load_animation{
	margin: auto;
	height: 2px;
	background-color: #646cff;
	animation: loadcenter .65s infinite alternate ease-in-out;
}




.boxOpenContainer{
	position: absolute;
	max-height: 275px;
	width: calc(100% - 32px);
	background: var(--hrm_palette-general-background-b);
	transition: opacity 0.25s ease-in;
	opacity: 0;
    border: 1px solid var(--color_text_dad);
	border-radius: 8px;
	overflow-y: auto;
	box-shadow: 0 2px 4px rgb(0 0 0 / 20%);
	z-index: 9;
} 